@use './functions-private'as f;

$current-theme: "default";

// generic color constants
$base-color-static: #000 !default;
$base-color: f.theme-transform(#000) !default;
$base-color-lightened-1: f.theme-transform(
  #4a5d75,
  $current-theme,
  0.25
) !default;
$base-color-lightened-2: f.theme-transform(
  #98a0ab,
  $current-theme,
  0.25
) !default;
$base-color-lightened-3: f.theme-transform(#b9bfc7, $current-theme) !default;
$base-color-lightened-4: f.theme-transform(#c1cdd5, $current-theme) !default;
$base-color-lightened-5: f.theme-transform(#dae1e6, $current-theme) !default;
$base-color-lightened-6: f.theme-transform(#e6eaef, $current-theme) !default;
$base-color-lightened-65: f.theme-transform(#f3f5f7, $current-theme) !default;
$base-color-lightened-7: f.theme-transform(#f8f9fa, $current-theme) !default;
$base-end-color-static: #fff !default;
$base-end-color: f.theme-transform(#fff, $current-theme) !default;
$base-background-color: var(
  --background-color
); //f.theme-transform(#f8f9fa, $current-theme) !default; //ebedf0 - alt for start page
$base-blue-color: #5963ff !default;
$base-blue-color-lightened: #7178e3 !default;
$base-red-color: #eb4e39 !default;
$base-green-color: #86cf2d !default;
$base-font-color: f.theme-transform(#3e4855, $current-theme, 0.25) !default;
$base-font-color-secondary: f.theme-transform(
  #98a0ab,
  $current-theme,
  0.75
) !default;
$base-font-color-secondary-half: f.theme-transform(
  #6b7480,
  $current-theme,
  0.75
) !default;

// box shadow constants
$box-shadow: 0px 4px 12px #33333322 !default;
$box-shadow-dark: 0px 6px 12px #3333 !default;

// border constant
$border-radius: 0.9375rem !default;
$border-radius-hard: calc(#{$border-radius} * 2) !default;
$border-radius-hardest: calc(#{$border-radius} * 10) !default;
$border-color: #eff0f1;
$border-color-dark-only: f.theme-transform(#00000000, $current-theme);
$border-width: 0.1875rem;
$border-width-thin: calc((#{$border-width} / 3) * 4);
$border-width-thick: calc(#{$border-width} * 2);

// @media constants

// Defaults
$lower-limit: 63em;
$middle-limit: 127em;
$upper-limit: 255em;

// Font Breaks
$font-break-0: 255em;
$font-break-1: 127em;
$font-break-2: 102em;
$font-break-25: 93em;
$font-break-3: 83.5em;
$font-break-4: 63em;

// Layout Breaks
$layout-break-0: 253em;
$layout-break-1: 173em;
$layout-break-2: 123em;
$layout-break-275: 93em;
$layout-break-3: 83em;
$layout-break-4: 73em;
$layout-break-5: 63em;
$layout-break-6: 43em;

// Offsets
$font-offset-mobile: 1.2;