@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  background-color: #F4F4F4;
}
html, body {
  height: 100%;
  margin: 0;
}
button:focus, input:focus, textarea:focus {
  outline: none;
}
p,
span,
H1,
H2,
H3,
H5,
H6,
h1,
h2,
h3,
h4,
h5,
h6,
small,
button,
div,
th,
td,
label,
input,
textarea {
  margin: 0;
  outline: none;
  resize: none;
  font-weight: 400;
  color: #3e4855;
}
th {
  text-align: inherit;
}
table {
  border-collapse: collapse;
}
a, a:-webkit-any-link {
  text-decoration: none;
}
input:not([type=radio]), textarea {
  -webkit-appearance: none;
  line-height: normal;
}
.noselection, button {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}
button {
  outline: none;
  border: none;
  border-radius: calc(0.9375rem * 2);
  background-color: #7178e3;
  color: #fff;
  padding: 1rem 2rem;
  font-size: 1.25rem;
  cursor: pointer;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.06em;
}
button img {
  height: 1rem;
}
button.selected, .button.selected {
  font-weight: 600 !important;
  color: #7178e3;
  border-color: #7178e3 !important;
}
.flex-column-wrap, .flex-column, .flex-row-wrap, .flex-row, .flex {
  display: flex;
}
.flex-row-wrap, .flex-row {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.flex-column-wrap, .flex-column {
  flex-direction: column;
}
.flex-column-wrap, .flex-row-wrap {
  flex-wrap: wrap;
}
.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
}
* {
  scrollbar-width: thin;
  scrollbar-color: #b9bfc7;
  font-family: "Roboto", serif;
  -webkit-tap-highlight-color: #fff0 !important;
}
body * {
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
*::-webkit-scrollbar {
  width: 0.5rem;
}
*::-webkit-scrollbar-track {
  background: #e6eaef;
  margin: 1.5rem 0;
  border-radius: 0.9375rem;
}
*::-webkit-scrollbar-thumb {
  background-color: #b9bfc7;
  border-radius: 0.9375rem;
  border: 0.1875rem solid #b9bfc7;
}
html {
  font-size: 16px;
  -webkit-text-size-adjust: none;
}
html,
body {
  background-color: var(--background-color);
  overflow: scroll;
  overflow-y: auto !important;
  overflow-x: hidden !important;
}
input {
  text-align: left;
  height: calc(2.5rem - 4px);
  padding: 0 0 0 2rem;
  border-radius: calc(0.9375rem * 2);
  background-color: #fff;
}
h1 {
  font-weight: 500;
}
textarea {
  border: 0.1875rem solid #eff0f1;
  border-radius: 0.9375rem;
  background-color: #fff;
}
BR,
br {
  height: 0;
  margin: 0.625rem 0;
}
.button {
  cursor: pointer;
}
button:hover {
  filter: invert(0.05);
}
.block {
  display: block;
}
.inline {
  display: inline-block;
}
@media (max-width: 255em) {
  html {
    font-size: 16px;
  }
}
@media (max-width: 127em) {
  html {
    font-size: 80%;
  }
}
@media (max-width: 102em) {
  html {
    font-size: 75%;
  }
}
@media (max-width: 93em) {
  html {
    font-size: 62.5%;
  }
}
@media (max-width: 83.5em) {
  html {
    font-size: 57.5%;
  }
}
@media (max-width: 63em) {
  html {
    font-size: 48%;
  }
}
